import React from "react";
import { Box, Meter } from "grommet";
import zxcvbn from "zxcvbn";
import styled from "styled-components";

const ColouredMeter = styled(Meter)``;

type StrengthMeterProps = {
  password: string;
};

export const StrengthMeter: React.FunctionComponent<StrengthMeterProps> = (
  props
) => {
  const { score } = zxcvbn(props.password);

  const cols = ["red", "red", "orange", "yellow", "green"];

  return (
    <Box pad="none" margin="none" width="stretch">
      <ColouredMeter
        background={cols[score]}
        values={[{ value: score }]}
        max={4}
        type="bar"
        thickness="small"
        margin="none"
        size="full"
      />
    </Box>
  );
};
